body{
  padding: 0;
  margin: 0;
  background-color: #f3f3f3;
 }
 .App{
  max-width: 1100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: auto;
 }
 .todo-wrap{
  display: table;
  margin:1em auto;
  width: 500px;
  padding: 1em;
  background-color: #086238;
  background-image: linear-gradient(#086238, #034a29);
  height: 95vh;
  overflow-y: auto;
  border-radius: 10px;
}
.todo-wrap.mrn-clr{
  background-image: linear-gradient(#800a49, #620838);
}
.todo-form-wrap{
  display: flex;
}
.todo-head{
  text-align: center;
  font-size: 2.2em;
  padding: 0 0 0.5em 0;
  color: aliceblue;
}
.todo-form-wrap input.form-control{
  border-top-right-radius:0 ;
  border-bottom-right-radius:0 ;
  font-size: 1.5rem;
}
.todo-form-wrap .btn{
  border-top-left-radius:0 ;
  border-bottom-left-radius:0 ;
}
.todo-form-wrap .btn .bi-play-circle-fill{
  font-size: 1.5em;
}
.todo-list-wrap-out{
  height: auto;
  max-height: 500px;
  margin: 1em auto;
  overflow-y: auto;
}

/* width */
.todo-list-wrap-out::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.todo-list-wrap-out::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
.todo-list-wrap-out::-webkit-scrollbar-thumb {
  background: #097945; 
  border-radius: 10px;
}

/* Handle on hover */
.todo-list-wrap-out::-webkit-scrollbar-thumb:hover {
  background: #208053; 
}
.todo-list-wrap{
  border: #12804d solid 1px;
  border-radius: 5px;
  height: auto;
  width: 99%;
}
.todo-list-wrap .todo-item-wrap{
  border-top: #12804d solid 1px;
  padding:0.6em 0.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.todo-list-wrap .todo-item-wrap:first-child{
  border-top: none;
}
.todo-list-wrap .todo-item-wrap .toto-item-text{
  color: #fff;
  font-size: 1.5em;
  max-width: 75%;
}
.todo-list-wrap .todo-item-wrap .check-btn,
.todo-list-wrap .todo-item-wrap .delete-btn{
  padding: 0;
  margin: 0;
  background: #f5f5f5;
  border: none;
  margin-left: 10px;
  width: 35px;
  height: 35px;
  border-radius: 35px;
}
.todo-list-wrap .todo-item-wrap .check-btn i.bi,
.todo-list-wrap .todo-item-wrap .delete-btn i.bi{
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 35px;
  font-size: 2em;
  display: block;
  border: none;
}
.todo-list-wrap .todo-item-wrap .check-btn i.bi{
  color: #034a29;
}
.todo-list-wrap .todo-item-wrap .delete-btn i.bi{
  color: #f30000cd;
}
.todo-list-wrap .todo-item-wrap i.bi::before{
  display: block;
  line-height: normal;
}
.todo-list-wrap .todo-item-wrap.task-done{
  background-color: rgb(197, 194, 194);
  text-decoration: line-through;
}
.todo-list-wrap .todo-item-wrap.task-done .check-btn i.bi,
.todo-list-wrap .todo-item-wrap.task-done .delete-btn i.bi{
  color: #ccc;
}
.todo-list-wrap.mrn-cvr{
  border-color: #8c476c;
}
.todo-list-wrap.mrn-cvr .todo-item-wrap{
border-color: #8c476c;
}
.btn-success.mrn-btn{
  background-color: #a73873;
  border-color: #872559;
}
.todo-list-wrap .todo-item-wrap .check-btn i.bi.bi-mrn{
 color: #620838;
}
.todo-list-wrap .todo-item-wrap.task-done .check-btn i.bi.bi-mrn{
  color: #ccc;
}
.todo-list-wrap .todo-item-wrap.mrn-btn.task-done{
  background-color: rgba(197, 194, 194,0.5);
}